import React, { useState } from 'react';
import ordinal_suffix_of from 'views/mobileView/components/atoms/ordinal-suffix';
import MainHeader from 'views/mobileView/components/molecules/Headers/MainHeader';
import cssVar from 'views/mobileView/styles';
import Tabs from 'views/mobileView/components/atoms/Tabs';
import { Chip } from '@mui/material';
import { AiOutlineRight} from 'react-icons/ai';
import { FiCalendar } from 'react-icons/fi';
import Modal from 'react-bootstrap/Modal';
import AdminServices from 'services/api/AdminServices';
import db from 'libs/db';
import { toast } from "react-toastify";
import { Backdrop } from '@mui/material';
import { CurrencyFormat } from 'views/mobileView/components/atoms/CurrencyFormat';
import MintboxIcon from 'views/mobileView/components/atoms/MintboxIcon';
import ActivityLoaderMintbox from 'views/mobileView/components/atoms/ActivityLoaderMintbox';
import executeSmallCaseTransaction from './ExecuteOrder';
import CurrencyInput from 'react-currency-input-field';
import AMCIcon from 'views/mobileView/components/atoms/AMCIcon';

export default function ConfirmAndInvestScreen(props) {
    const FUNDDATA = props.location.state.fundData || {}
    const SCREENTYPE = props.location.state.type || 3;
    let MINSIPAMOUNT = FUNDDATA.MinSIPAmount;
    // if(SCREENTYPE === 1){
    //     MINSIPAMOUNT = MINSIPAMOUNT > 500 ? MINSIPAMOUNT : 500
    // }
    const isDefaultSIP = props.location.state.sip || false;

    const [onetimeAmount, setOnetimeAmount] = React.useState();
    const [sipAmount, setSipAmount] = React.useState();
    const [sipDate, setSipDate] = React.useState(Number(new Date().getDate())<28?new Date().getDate():28);

    const [loading, setLoading] = React.useState(false);
    const [trxnLoading, setTrxnLoading] = React.useState(false);
    const [showMarketClosedModal, setShowMarketClosedModal] = useState(false);
    const [showDateModal, setShowDateModal] = useState(false);
    const [showPortfolioModal, setShowPortfolioModal] = useState(false);

    React.useEffect(async () => {
        db.get(db.model.user).then(async (user) => {
            await db.get(db.model.clientData).then(async (p) => {
                if (p === null) {
                    let res = await AdminServices.getClient(user.userData.id).then(res => res);
                    if (res.type === true) {
                        const a = JSON.parse(res.data);
                        let aa = a.reduce((aa, f) => { return { ...aa, ...f } }, {})
                        console.log("Get Client API :", aa)
                        await db.set(db.model.clientData, JSON.stringify(aa))
                        if (SCREENTYPE !== 2 && aa.SmallcaseInfo.length === 0) {
                            props.history.replace({ pathname: '/link-demat', state: { ClientId: res.Client[0].ClientId, type: SCREENTYPE } })
                        } else if(SCREENTYPE === 2 && aa.Client[0].IsProfileComplete !== 1){
                            props.history.replace({ pathname: '/link-demat', state: { type: SCREENTYPE } })
                        }
                    }
                } else {
                    if (SCREENTYPE !== 2 && p.SmallcaseInfo.length === 0) {
                        props.history.replace({ pathname: '/link-demat', state: { ClientId: p.Client[0].ClientId, type: SCREENTYPE } })
                    } else if(SCREENTYPE === 2 && p.Client[0].IsProfileComplete !== 1){
                        props.history.replace({ pathname: '/link-demat', state: { type: SCREENTYPE } })
                    }
                }
            })
        })

        if (SCREENTYPE === 3) {
            if (!FUNDDATA.Constituents) {
                setLoading(true)
                let res = await AdminServices.getMintboxConstituentsById(FUNDDATA.MintboxId).then(res => res)
                if (res.type) {
                    console.log("Get mintbox constituents API", res.data)
                    FUNDDATA.Constituents = res.data
                    setLoading(false)
                } else {
                    toast.error("Error")
                    setLoading(false)
                }
            }
        }
        console.log("fund:", FUNDDATA)
        console.log("screen type", SCREENTYPE)
    }, [])

    const getFundQunatities = async (etfs, constituents, amount) => {
        var etfsData = await AdminServices.getETFsById(etfs.toString()).then(res => res)
        console.log(etfsData)
        if (etfsData.type) {
            console.log(etfsData)
            const weight = {};
            constituents.map((item) => {
                weight[item.Symbol] = item.CurrentWeightage / 100
            })
            const currentNav = {};
            etfsData.data.map(item => {
                currentNav[item.Symbol] = item.NAV
            })
            var dataFinal = {
                "weights": weight,
                "currentNav": currentNav,
                "investment": amount
            }
            console.log(JSON.stringify(dataFinal))
            var finalDATA = await AdminServices.getETFQuantities(JSON.stringify(dataFinal)).then(res => res)
            if (finalDATA.type) {
                var quant = finalDATA.data.Result
                quant = JSON.parse(quant).data
                console.log(finalDATA)
                console.log(quant)
                return quant
            } else {
                setTrxnLoading(false);
                toast.error('Error')
            }
        } else {
            setTrxnLoading(false);
            toast.error('Error')
        }

    }

    const createOrder = async (amount, isSIP) => {
        console.log("crt ordr", isSIP, amount)
        setTrxnLoading(true);
        if (SCREENTYPE === 1) {
            var constituents = [{
                "Symbol": FUNDDATA.FundCode,
                "CurrentWeightage": 100
            }]
            var orderQuant = await getFundQunatities([FUNDDATA.FundCode], constituents, amount)
            const securities = Object.entries(orderQuant).map((e) => ({ "ticker": e[0], "quantity": e[1] + "", type: "BUY" }));
            console.log(securities)
            let res = await executeSmallCaseTransaction({
                navigation: props.history,
                securities: securities,
                amount: amount,
                isSIP: isSIP,
                isUnitsBased: false,
                units: 0,
                sipDate: sipDate,
                SCREENTYPE: 1,
                FUNDDATA: FUNDDATA,
                isInvest: true
            })
            console.log(res)
            if(res === 'market_closed'){
                setShowMarketClosedModal(true);
            }
            setTrxnLoading(false)
        } else if (SCREENTYPE === 3) {
            var etfs = FUNDDATA.Constituents.map(etf => etf.Symbol)
            var orderQuant = await getFundQunatities(etfs, FUNDDATA.Constituents, amount)
            const securities = Object.entries(orderQuant).map((e) => ({ "ticker": e[0], "quantity": e[1] + "", type: "BUY" }));
            console.log(securities)
            let res = await executeSmallCaseTransaction({
                navigation: props.history,
                securities: securities,
                amount: amount,
                isSIP: isSIP,
                isUnitsBased: false,
                units: 0,
                sipDate: sipDate,
                SCREENTYPE: 3,
                FUNDDATA: FUNDDATA,
                isInvest: true
            })
            setTrxnLoading(false)
            console.log(res)
            if(res === 'market_closed'){
                setShowMarketClosedModal(true);
            }
        } else if (SCREENTYPE === 2) {
            toast.error("MF investments are paused")
        }
    }

    const OneTime = () => {
        const chipset = [
            { "key": 5000, "name": "+ ₹ 5,000" },
            { "key": 10000, "name": "+ ₹ 10,000" },
            { "key": 50000, "name": "+ ₹ 50,000" }
        ]

        return (
            <>
                <div style={{ padding: '4%' }}>
                    <div className='header1dark' style={{ paddingTop: '4%' }}>
                        Investment amount
                    </div>
                    <div className='smallTextLight' style={{ paddingTop: '1%', paddingBottom: "2%" }}>
                        Minimum required <span className='smallTextDark'>{CurrencyFormat(MINSIPAMOUNT, 'long', 2)}</span>
                    </div>
                    <CurrencyInput
                        className='normalInput rupee'
                        id="onetimeAmount"
                        name="onetimeAmount"
                        placeholder="10,000"
                        value={onetimeAmount}
                        allowDecimals={false}
                        allowNegativeValue={false}
                        disableAbbreviations={false}
                        prefix=' '
                        onValueChange={(value) => setOnetimeAmount(value)}
                        intlConfig={{ locale: 'en-IN', currency: 'INR' }}
                    />
                    <div style={{ display: 'flex', paddingTop: "1%" }}>
                        {chipset.map((item) => {
                            return (
                                <Chip
                                    className="normalChip"
                                    key={item.key}
                                    label={item.name}
                                    onClick={() => {
                                        setOnetimeAmount(Number(onetimeAmount || 0) + item.key);
                                    }}
                                />
                            )
                        })}
                    </div>
                </div>
                {SCREENTYPE !== 2 ?
                    <div className='bodyTextLight' style={{ position: "fixed", bottom: "70px", width: "100%", padding: "2% 4%", borderTop: "1px solid " + cssVar.getPropertyValue('--light'), backgroundColor: cssVar.getPropertyValue('--white') }}>
                        Note: The actual amount invested may be slightly different due to constantly changing prices
                    </div>
                    : ''
                }
                <div className='footer'>
                    <button
                        className='solidButton'
                        style={{ opacity: Number(onetimeAmount) >= MINSIPAMOUNT ? 1 : 0.4 }}
                        onClick={() => {
                            if (Number(onetimeAmount) >= MINSIPAMOUNT) {
                                createOrder(Number(onetimeAmount), false)
                            } else {
                                toast.error("Min investment amount is " + MINSIPAMOUNT)
                            }
                        }}
                    >
                        Confirm and Invest
                    </button>
                </div>
            </>
        )
    }

    const SIP = () => {

        const chipset = [
            { "key": 5000, "name": "₹ 5,000" },
            { "key": 10000, "name": "₹ 10,000" },
            { "key": 50000, "name": "₹ 50,000" }
        ]

        const DateModal = () => {
            const [selectedDate, setSelectedDate] = React.useState(sipDate)
            return (
                <Modal
                    className="bottom-drawer"
                    show={showDateModal}
                    onHide={() => setShowDateModal(false)}
                    keyboard={false}
                >
                    <Modal.Body>
                        <div style={{ display: "flex", justifyContent: "center", gap: "20px", paddingTop: "20px" }}>
                            {
                                [1, 2, 3, 4, 5, 6, 7].map(d => <div key={d} onClick={() => setSelectedDate(d)} style={{ border: "1px solid #e5e5e5", borderRadius: '32px', height: "32px", width: '32px', textAlign: "center", paddingTop: "2px", color: d === selectedDate ? "#ffffff" : "#6c6c6c", background: d === selectedDate ? cssVar.getPropertyValue('--darkGreen') : 'transparent' }}>{d}</div>)
                            }
                        </div>
                        <div style={{ display: "flex", justifyContent: "center", gap: "20px", paddingTop: "20px" }}>
                            {
                                [8, 9, 10, 11, 12, 13, 14].map(d => <div key={d} onClick={() => setSelectedDate(d)} style={{ border: "1px solid #e5e5e5", borderRadius: '32px', height: "32px", width: '32px', textAlign: "center", paddingTop: "2px", color: d === selectedDate ? "#ffffff" : "#6c6c6c", background: d === selectedDate ? cssVar.getPropertyValue('--darkGreen') : 'transparent' }}>{d}</div>)
                            }
                        </div>
                        <div style={{ display: "flex", justifyContent: "center", gap: "20px", paddingTop: "20px" }}>
                            {
                                [15, 16, 17, 18, 19, 20, 21].map(d => <div key={d} onClick={() => setSelectedDate(d)} style={{ border: "1px solid #e5e5e5", borderRadius: '32px', height: "32px", width: '32px', textAlign: "center", paddingTop: "2px", color: d === selectedDate ? "#ffffff" : "#6c6c6c", background: d === selectedDate ? cssVar.getPropertyValue('--darkGreen') : 'transparent' }}>{d}</div>)
                            }
                        </div>
                        <div style={{ display: "flex", justifyContent: "center", gap: "20px", paddingTop: "20px" }}>
                            {
                                [22, 23, 24, 25, 26, 27, 28].map(d => <div key={d} onClick={() => setSelectedDate(d)} style={{ border: "1px solid #e5e5e5", borderRadius: '32px', height: "32px", width: '32px', textAlign: "center", paddingTop: "2px", color: d === selectedDate ? "#ffffff" : "#6c6c6c", background: d === selectedDate ? cssVar.getPropertyValue('--darkGreen') : 'transparent' }}>{d}</div>)
                            }
                        </div>
                    </Modal.Body>
                    <Modal.Footer>
                        <button className='outlineButton' onClick={() => setShowDateModal(false)}>
                            Close
                        </button>
                        <button className="solidButton" onClick={() => {
                            setSipDate(selectedDate);
                            setShowDateModal(false)
                        }}>
                            Confirm
                        </button>
                    </Modal.Footer>
                </Modal>
            )
        }

        return (
            <>
                <DateModal />
                <div style={{ margin: '4%' }}>
                    <div className='header1dark' style={{ paddingTop: '4%' }}>
                        Monthly SIP amount
                    </div>
                    <div className='smallTextLight' style={{ paddingTop: '1%', paddingBottom: "2%" }}>
                        Minimum required <span className='smallTextDark'>{CurrencyFormat(MINSIPAMOUNT, 'long', 2)}</span>
                    </div>
                    <CurrencyInput
                        className='normalInput rupee'
                        id="sipAmount"
                        name="sipAmount"
                        placeholder="10,000"
                        value={sipAmount}
                        allowDecimals={false}
                        allowNegativeValue={false}
                        disableAbbreviations={false}
                        prefix=' '
                        onValueChange={(value) => setSipAmount(value)}
                        intlConfig={{ locale: 'en-IN', currency: 'INR' }}
                    />
                    <div style={{ display: 'flex', paddingTop: '1%' }}>
                        {chipset.map((item) => {
                            return (
                                <Chip
                                    className={sipAmount === item.key ? "normalChip selected" : "normalChip"}
                                    key={item.key}
                                    label={item.name}
                                    onClick={() => {
                                        setSipAmount(item.key);
                                    }}
                                />
                            )
                        })}
                    </div>
                    <div style={{ paddingTop: '42px' }}>
                        <FiCalendar size={24} className='secondaryGreen' />
                        <div className='bodyTextLight' style={{ paddingTop: "2%", paddingBottom: "1%" }}>
                            <span className='bigHeader' style={{ fontSize: "20px" }}>{ordinal_suffix_of(sipDate)}</span> of every month
                        </div>
                        <div className='header2dark secondaryGreen' onClick={() => setShowDateModal(true)}>
                            Change <AiOutlineRight size={12} color="#294C88" style={{ verticalAlign: 'unset' }} />
                        </div>
                    </div>
                    <div style={{ marginTop: "10%", padding: '4%', background: cssVar.getPropertyValue('--lightest'), border: "1px solid " + cssVar.getPropertyValue('--light'), borderRadius: "8px" }}>
                        <div className='header2dark' style={{ color: cssVar.getPropertyValue('--darkGreen'), paddingBottom: "1%" }}>Note</div>
                        <div className='bodyTextLight'>
                            First investment will be done today and monthly SIPs will start from {ordinal_suffix_of(sipDate)} of next month
                        </div>
                    </div>
                </div>
                {SCREENTYPE !== 2 ?
                    <div className='bodyTextLight' style={{ position: "fixed", bottom: "70px", width: "100%", padding: "2% 4%", borderTop: "1px solid " + cssVar.getPropertyValue('--light'), backgroundColor: cssVar.getPropertyValue('--white') }}>
                        Note: The actual amount invested may be slightly different due to constantly changing prices
                    </div>
                    : ''
                }
                <div className='footer'>
                    <button
                        className='solidButton'
                        style={{ opacity: Number(sipAmount) >= MINSIPAMOUNT ? 1 : 0.4 }}
                        onClick={() => {
                            if (Number(sipAmount) >= MINSIPAMOUNT) {
                                createOrder(Number(sipAmount), true)
                            } else {
                                toast.error("Min investment amount is " + MINSIPAMOUNT)
                            }
                        }}
                    >
                        Confirm and Invest
                    </button>
                </div>
            </>
        )
    }

    const keyTitles = [
        { title: "One-time investment", view: OneTime(), show:true },
        { title: "Monthly SIP", view: SIP(),show:!(SCREENTYPE===1 && FUNDDATA.MintboxId) },
    ];

    // const PortfolioModal = () => {
    //     return (
    //         <Modal
    //             className="bottom-drawer"
    //             show={showPortfolioModal}
    //             onHide={() => setShowPortfolioModal(false)}
    //             keyboard={false}
    //         >
    //             <Modal.Body style={{ height: "70vh", overflow: "scroll" }}>
    //                 <FundConstituents fundConstituents={FUNDDATA.Constituents} />
    //             </Modal.Body>
    //             <Modal.Footer>
    //                 <button className="close" onClick={() => setShowPortfolioModal(false)}>
    //                     Close
    //                 </button>
    //             </Modal.Footer>
    //         </Modal>
    //     )
    // }

    const MarketClosedModal = () => {
            return (
                <Modal
                    className="bottom-drawer"
                    show={showMarketClosedModal}
                    onHide={() => setShowMarketClosedModal(false)}
                    keyboard={false}
                >
                    <Modal.Body>
                        <div style={{display:'flex',gap:'4%'}}>
                            <FiCalendar size={20} className='secondaryGreen'/>
                            <div className="bodyTextDark" style={{flex:1}}>
                            You cannot place order outside of market hours. We will soon be launching a solution that lets you place post-market orders. Till then, see you when the markets opens!
                            </div>
                        </div>
                    </Modal.Body>
                    <Modal.Footer>
                        <button className="solidButton" onClick={() => setShowMarketClosedModal(false)}>
                            Close
                        </button>
                    </Modal.Footer>
                </Modal>
            )
        }

    if (loading) {
        return (
            <div style={{ flex: 1 }}>
                <MainHeader
                    header="Invest"
                    navigation={props.history}
                />
                <ActivityLoaderMintbox />
            </div>
        );
    }
    else return (
        <div className='mainContainer'>
            <MainHeader
                navigation={props.history}
                header="Invest"
            />
            <div className="dataContainer px-0" style={{ paddingBottom: '120px' }}>
                <div style={{ display: 'flex', alignItems: "center", gap: '4%', padding: '4%' }}>
                    {
                        SCREENTYPE === 3 ?
                            <MintboxIcon id={FUNDDATA.MintboxId} height="40px" width="40px" />
                            :
                            <AMCIcon amcName={FUNDDATA.AMCName} height="40px" width="40px" />
                    }
                    <div className='header1dark' style={{ flex: 1 }}>
                        {SCREENTYPE === 1 ? FUNDDATA.ETFName : SCREENTYPE === 2 ? FUNDDATA.SchemeName : FUNDDATA.MintboxName}
                    </div>
                    {/* {
                        SCREENTYPE === 3?
                        <div style={{ fontSize: "14px", fontWeight: "500", color: "#294C88" }} onClick={() => setShowPortfolioModal(true)}>Show portfolio <AiOutlineDown /></div>
                        :
                        <></>
                    } */}
                </div>
                <Tabs tabsObject={keyTitles.filter(k=>k.show)} color={cssVar.getPropertyValue('--darkGreen')} defaultTab={isDefaultSIP ? 1 : 0}/>
            </div>
            <Backdrop style={{ color: '#fff', zIndex: 1021 }} open={trxnLoading}>
                <ActivityLoaderMintbox />
            </Backdrop>
            {/* <PortfolioModal/> */}
            <MarketClosedModal/>
        </div>
    );
}
